import { NavBar } from "antd-mobile";
import React, { useState, useEffect } from "react";
import { Btn, Icon } from "../buttons";
import { Link, useParams } from "react-router-dom";

import { withRouter } from "react-router-dom";

const BottomNav = (props) => {
  const [isOnAccountPage, setIsOnAccountPage] = useState(
    props.location.pathname.includes("/account")
  );

  const nav1 = <Btn text="Account" icon={<Icon icon="mail" />} />;
  const nav2 = <Btn text="Folder" icon={<Icon icon="folder" />} />;
  const leftComps = isOnAccountPage ? nav2 : nav1;

  useEffect(() => {
    setIsOnAccountPage(props.location.pathname.includes("/account"));
  });

  return (
    <NavBar
      style={{
        backgroundColor: "#e4e8ec",
        position: "fixed",
        bottom: 0,
        width: "100%",
      }}
      leftContent={leftComps}
      rightContent={<Btn text="Edit" />}
    />
  );
};

export default withRouter(BottomNav);
